
import React, { useState, useRef } from 'react';
import { Button, Card, CardContent, Container, Grid, TextField, } from '@mui/material';
import QRCode from 'qrcode';
import QrReader from 'react-qr-scanner';
import express from 'cors'

const cors =require('cors')
const app = express()
app.use(cors())


export function Qr() {

    const [text, setText] = useState('')
    const [imageUrl, setImageUrl] = useState('')

    const [scanResultFile, setScanResultFile] = useState('');
    const qrRef = useRef(null)

    const [scanResultWebCam, setScanResultWebCam] = useState('');

    const generarQr = async () => {
        try {
            const responde = await QRCode.toDataURL(text)
            setImageUrl(responde)
        } catch (error) {
            console.error(error);
        }
    }
    /* Lector de codigo QR */
   /*  const handleErrorFille = (error) => {
        console.log(error)
    }
    const handleScanFile = (result) => {
        if (result) {
            setScanResultFile(result)
        }
    }

    const onScanfile = () => {
        qrRef.current.openImageDialog();
    } */


    const handleErrorWebCam = (error) => {
        console.log(error);
    }

    const handleScanWebCam = (result) => {
        if (result) {
            setScanResultWebCam(result);
        }
    }



    return (
        <Container >
            <Card>
                <CardContent>
                    {/* Generador de codigo QR */}
                    <Grid container spacing={2}>
                        <Grid item xl={4} lg={4} md={6} sm={12} xs={12}>
                            <TextField label="Ingrese el texto" onChange={(e) => setText(e.target.value)} />
                            <Button variant='contained' color="primary" onClick={() => generarQr()} > Generar</Button>
                            <br />
                            <br />
                            {imageUrl ? (<a href={imageUrl} download> <img src={imageUrl} alt='img' /> </a>) : null}

                        </Grid>
                        {/* Escanear codigo Qr con imagen*/}
                        {/* <Grid item xl={4} lg={4} md={6} sm={12} xs={12}>
                            <Button variant='contained' color='secundary' onClick={onScanfile}> Escanear codigo QR</Button>
                            <QrReader
                                href={qrRef}
                                delay={300}
                                style={{ whidt: '100%' }}
                                onError={handleErrorFille}
                                onScan={handleScanFile}
                                legacyMode
                            />
                            <h3>Mensaje del código QR: {scanResultFile} </h3>
                        </Grid> */}

                        <Grid item xl={4} lg={4} md={6} sm={12} xs={12}>
                            <h3>Leer códogo Qr Con web cam</h3>
                            <QrReader
                                delay={300}
                                style={{ width: '100%' }}
                                onError={handleErrorWebCam}
                                onScan={handleScanWebCam}
                            />
                            <h3>Scanned By WebCam Code: {scanResultWebCam}</h3>
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
        </Container >
    )

}

export default Qr;